<template>
  <button @click="btnClickEvent">
    <slot name="icon"> </slot>
    <span>{{ text }}</span>
  </button>
</template>

<script>
export default {
  props: {
    text: {
      default: ""
    },
    msg: {
      default: ""
    }
  },
  methods: {
    // 
    btnClickEvent: function() {
      this.$emit("btnClickEvent");
    }
  }
};
</script>
